* {
  margin: 0;
  padding: 0;
}
/*Full wrapper for intro to cover whole screen*/

#projects-container {
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100vh;
	width: 100%;
	background: linear-gradient(171.08deg, var(--black)43.25%, var(--darkblue)68.65%);
}



.carousel-container {
	margin-top: auto;
	margin-bottom: auto;
	height: 50%;
	width: 50%;
}

.aws-btn {

--slider-height-percentage: 62%;
--slider-transition-duration: 200ms;
--organic-arrow-thickness: 10px;
--organic-arrow-border-radius: 30px;
--organic-arrow-height: 50px;
--organic-arrow-color: #8b9db1;
--control-button-width: 10%;
--control-button-height: 25%;
--control-button-background: transparent;
--control-bullet-color: #d63b6b;
--control-bullet-active-color: #b6325b;
--loader-bar-color: #851515;
--loader-bar-height: 5px;
}