.animatedTextContainer {
  --maskX: 0;
  --maskY: 0;
  position: relative;
  cursor: default;
  user-select:none;
  width: 100vw;
}

.animatedTextContent {
  color: var(--purple);
  font-weight: 600;
  letter-spacing: 5px;
  text-shadow: 5px 3px 8px rgba(0, 0, 0, 0.2);
  margin: 0;
}

.animatedTextContentClone {
  width: 100vw;
  composes: animatedTextContent;
  position: absolute;
  left: 0;
  top: 0;
  color: var(--offwhite);
  transition: all 0.4s ease-out;
  clip-path: polygon(
    0 0,
    calc(var(--maskX) * 1% + (var(--maskY) - 50) * 0.4%) 0,
    calc(var(--maskX) * 1% + (var(--maskY) - 50) * -0.4%) 100%,
    0 100%
  );
}
